function decodeMetadata(buffer) {
  const metadata = borsh.deserializeUnchecked(
    METADATA_SCHEMA,
    Metadata,
    buffer,
  );
  return metadata;
}
 
const bytePublicKeyToString = (bytes: any): string => {
  return new PublicKey(bytes).toBase58()
}

