import "./App.css";
import { useMemo } from "react";

import Home from "./components/Home";

import * as anchor from "@project-serum/anchor";
import { clusterApiUrl } from "@solana/web3.js";

import {
  getPhantomWallet,
  getSlopeWallet,
  getSolflareWallet,
  getSolletWallet,
  getSolletExtensionWallet,
  getLedgerWallet,
  getBloctoWallet,
} from "@solana/wallet-adapter-wallets";

import {
  ConnectionProvider,
  WalletProvider,
} from "@solana/wallet-adapter-react";

import { WalletDialogProvider } from "@solana/wallet-adapter-material-ui";
import { createTheme, ThemeProvider } from "@material-ui/core";
import { NFTList } from "./components/NFTList"
import { CANDY_MACHINE_CONFIG, CANDY_MACHINE_ID, NETWORK, TREASURY } from "./lib/constants"


const rpcHost = `https://explorer-api.${NETWORK}.solana.com`;

const connection = new anchor.web3.Connection(rpcHost);

const startDateSeed = parseInt('16326088986130000', 10);

const txTimeout = 30000; // milliseconds (confirm this works for your project)

const theme = createTheme({
    palette: {
        type: 'dark',
    },
    overrides: {
        MuiButtonBase: {
            root: {
                justifyContent: 'flex-start',
            },
        },
        MuiButton: {
            root: {
                textTransform: undefined,
                padding: '12px 16px',
            },
            startIcon: {
                marginRight: 8,
            },
            endIcon: {
                marginLeft: 8,
            },
        },
    },
});

const App = () => {
  const endpoint = useMemo(() => clusterApiUrl(NETWORK), []);

  const wallets = useMemo(
    () => [
      getPhantomWallet(),
      getSlopeWallet(),
      getSolflareWallet(),
      getSolletWallet({ network: NETWORK }),
      getSolletExtensionWallet({ network: NETWORK }),
      getLedgerWallet(),
      getBloctoWallet({ network: NETWORK }),
    ],
    []
  );

  return (
      <ThemeProvider theme={theme}>
        <ConnectionProvider endpoint={endpoint}>
          <WalletProvider wallets={wallets} autoConnect>
            <WalletDialogProvider>
              <Home
                candyMachineId={CANDY_MACHINE_ID}
                config={CANDY_MACHINE_CONFIG}
                connection={connection}
                startDate={startDateSeed}
                treasury={TREASURY}
                txTimeout={txTimeout}
              />
              <NFTList />
            </WalletDialogProvider>
          </WalletProvider>
        </ConnectionProvider>
      </ThemeProvider>
  );
};

export default App;